.clicker {
  cursor: pointer;
}

.pulse {
  transition: transform 0.1s, filter 0.2s;
  background-color: #f0f0f0;
  animation: pulse-animation 0.2s ease-in-out;
}

@keyframes pulse-animation {
  0% {
    transform: scale(1);
    filter: brightness(100%);
  }
  50% {
    transform: scale(1.1);
    filter: brightness(150%);
  }
  100% {
    transform: scale(1);
    filter: brightness(100%);
  }
}
